@font-face {
  font-family: Canela;
  src: local('Canela'),
       url(./assets/fonts/canela.eot?) format('eot'),
       url(./assets/fonts/canela.woff2) format('woff2');
      font-weight: normal;
      font-style: normal;
      font-display: swap;
}

@font-face {
    font-family: Canela;
    src: local('Canela'),
         url(./assets/fonts/Canela-Medium.eot),
         url(./assets/fonts/Canela-Medium.woff2) format('woff2'),
         url(./assets/fonts/Canela-Medium.woff) format('woff');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

@font-face {
  font-family: Canela;
  src: local('Canela'),
      url(./assets/fonts/canela-Bold.eot?) format('eot'),
      url(./assets/fonts/canela-Bold.woff2) format('woff2'),
      url(./assets/fonts/canela_bold.otf) format('otf');
      font-weight: bold;
      font-style: normal;
      font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: local('Inter'),
       url(./assets/fonts/Inter-Regular.eot);
  src: url(./assets/fonts/Inter-Regular.eot?#iefix) format('embedded-opentype'),
      url(./assets/fonts/Inter-Regular.woff2) format('woff2'),
      url(./assets/fonts/Inter-Regular.woff) format('woff');
      font-weight: normal;
      font-style: normal;
      font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: local('Inter'),
       url(./assets/fonts/Inter-Bold.eot);
  src: url(./assets/fonts/Inter-Bold.eot?#iefix) format('embedded-opentype'),
      url(./assets/fonts/Inter-Bold.woff2) format('woff2'),
      url(./assets/fonts/Inter-Bold.woff) format('woff');
      font-weight: bold;
      font-style: normal;
      font-display: swap;
}